@font-face {
  font-family: "Roboto";
  font-style: normal;
  font-weight: 400;
  src: url("../fonts/roboto-v30-latin_cyrillic-regular.woff2") format("woff2"), url("../fonts/roboto-v30-latin_cyrillic-regular.woff") format("woff");
}
@font-face {
  font-family: "Roboto";
  font-style: normal;
  font-weight: 500;
  src: url("../fonts/roboto-v30-latin_cyrillic-500.woff2") format("woff2"), url("../fonts/roboto-v30-latin_cyrillic-500.woff") format("woff");
}
@font-face {
  font-family: "Roboto";
  font-style: normal;
  font-weight: 700;
  src: url("../fonts/roboto-v30-latin_cyrillic-700.woff2") format("woff2"), url("../fonts/roboto-v30-latin_cyrillic-700.woff") format("woff");
}
@font-face {
  font-family: "Roboto";
  font-style: normal;
  font-weight: 900;
  src: url("../fonts/roboto-v30-latin_cyrillic-900.woff2") format("woff2"), url("../fonts/roboto-v30-latin_cyrillic-900.woff") format("woff");
}
@font-face {
  font-family: "Raleway";
  font-style: normal;
  font-weight: 700;
  src: url("../fonts/raleway-v28-latin_cyrillic-700.woff2") format("woff2"), url("../fonts/raleway-v28-latin_cyrillic-700.woff") format("woff");
}
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body {
  font-family: "Roboto", "Arial", sans-serif;
  font-style: normal;
  font-weight: 400;
}

a {
  text-decoration: none;
}

button {
  cursor: pointer;
}

.conteiner {
  max-width: 1200px;
  margin: 0 auto;
  padding: 0 15px;
}

.no-scroll {
  overflow: hidden;
}

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  clip: rect(0 0 0 0);
  overflow: hidden;
}

.btn {
  cursor: pointer;
  font-weight: 700;
  font-size: 16px;
  line-height: 30px;
  display: flex;
  justify-content: center;
  align-items: center;
  text-align: center;
  letter-spacing: 0.06em;
  color: #FFFFFF;
  background: #2196F3;
  box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.15);
  border-radius: 4px;
  width: 200px;
  height: 50px;
  border: none;
}
.btn:hover {
  background: #1b7bc8;
}
.btn:active {
  background: #188CE8;
}

.header {
  position: relative;
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding-top: 16px;
  padding-bottom: 16px;
}
.header__logo {
  font-family: "Raleway", "Arial", sans-serif;
  font-style: normal;
  font-weight: 700;
  font-size: 24px;
  line-height: 28px;
  letter-spacing: 0.03em;
  color: black;
}
.header__logo span {
  color: #2196F3;
}
.header__main {
  display: flex;
  flex-direction: column;
  position: fixed;
  background-color: white;
  width: 100%;
  height: 100vh;
  top: 0px;
  left: 0px;
  padding: 21px 25px 48px 40px;
}
.header__main-cross {
  align-self: flex-end;
  display: flex;
  flex-direction: column;
}
.header__main-cross span {
  position: absolute;
  display: block;
  width: 24px;
  height: 3px;
  background: black;
  right: 25px;
}
.header__main-cross-one {
  transform-origin: center;
  transform: rotate(45deg);
}
.header__main-cross-two {
  transform-origin: center;
  transform: rotate(-45deg);
}
.header__main-cross:hover span {
  background: #2196F3;
}
.header__main-menu {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  margin-top: 27px;
  margin-bottom: auto;
}
.header__main-menu-link {
  font-style: normal;
  font-weight: 500;
  font-size: 40px;
  line-height: 47px;
  letter-spacing: 0.02em;
  color: #212121;
  margin-bottom: 35px;
}
.header__main-menu-link:hover {
  color: #2196F3;
}
.header__main-tel {
  font-weight: 500;
  font-size: 34px;
  line-height: 40px;
  letter-spacing: 0.02em;
  color: #2196F3;
  margin-bottom: 32px;
}
.header__main-email {
  font-weight: 500;
  font-size: 24px;
  line-height: 28px;
  letter-spacing: 0.02em;
  color: #757575;
  margin-bottom: 64px;
}
.header__main-social {
  display: flex;
  flex-wrap: wrap;
}
.header__main-social-link {
  font-weight: 500;
  font-size: 18px;
  line-height: 22px;
  letter-spacing: 0.02em;
  color: #2196F3;
  padding: 0 10px;
}
.header__main-social-link:nth-child(1) {
  padding-left: 0;
}
.header__main-social-link:nth-child(4) {
  padding-right: 0;
}
.header__main-social-link:not(:last-child) {
  border-right: 1px solid rgba(33, 33, 33, 0.2);
}
.header__main-pic {
  display: none;
}
.header__main.is-hidden {
  display: none;
}
.header__burger {
  cursor: pointer;
  display: flex;
  flex-direction: column;
  gap: 4px;
}
.header__burger span {
  display: block;
  width: 24px;
  height: 3px;
  background: #212121;
}
.header__burger:hover span {
  background: #2196F3;
}

@media screen and (min-width: 768px) {
  .header {
    display: grid;
    grid-template-columns: min-content 1fr;
    padding-top: 21px;
    padding-bottom: 0;
    align-items: flex-start;
  }
  .header__main {
    position: static;
    align-items: center;
    grid-template-columns: 1fr max-content;
    grid-template-rows: max-content min-content;
    width: auto;
    height: auto;
    padding: 0;
  }
  .header__main-cross {
    display: none;
  }
  .header__main-menu {
    grid-row: 1/3;
    flex-direction: row;
    justify-self: center;
    margin: 0;
    gap: 50px;
  }
  .header__main-menu-link {
    position: relative;
    font-weight: 500;
    font-size: 14px;
    line-height: 16px;
    letter-spacing: 0.02em;
    color: #212121;
    margin-bottom: 0;
    padding-top: 6px;
    padding-bottom: 32px;
  }
  .header__main-menu-link:hover::before {
    content: "";
    position: absolute;
    display: block;
    height: 4px;
    width: 100%;
    border-radius: 2px;
    background: #2196F3;
    bottom: 0;
  }
  .header__main-social {
    display: none;
  }
  .header__main-tel {
    display: flex;
    align-items: center;
    font-weight: 500;
    font-size: 12px;
    line-height: 14px;
    letter-spacing: 0.02em;
    color: #757575;
    margin: 0;
    margin-right: 14px;
    align-self: start;
    justify-self: end;
  }
  .header__main-tel:hover {
    color: #2196F3;
  }
  .header__main-tel:hover path {
    fill: #2196F3;
  }
  .header__main-email {
    display: flex;
    align-items: center;
    grid-row: 1/2;
    font-weight: 500;
    font-size: 12px;
    line-height: 14px;
    letter-spacing: 0.02em;
    color: #757575;
    margin: 0;
    justify-self: end;
  }
  .header__main-email:hover {
    color: #2196F3;
  }
  .header__main-email:hover path {
    fill: #2196F3;
  }
  .header__main-pic {
    display: block;
    margin-right: 8px;
  }
  .header__main.is-hidden {
    display: grid;
  }
  .header__burger {
    display: none;
  }
}
@media screen and (min-width: 1200px) {
  .header__main {
    grid-template-columns: 1fr max-content max-content;
  }
  .header__main-tel {
    font-size: 14px;
    margin-top: 11px;
    margin-right: 0;
    grid-row: 1/3;
    grid-column: 3/4;
  }
  .header__main-email {
    font-size: 14px;
    margin-top: 11px;
    margin-right: 30px;
    grid-row: 1/3;
    align-self: start;
  }
}
