}
.port__nav {
  display: flex;
  flex-wrap: wrap;
  gap: 15px 8px;
  padding-bottom: 40px;
}
.port__nav-btn {
  font-weight: 500;
  font-size: 16px;
  line-height: 26px;
  text-align: center;
  letter-spacing: 0.03em;
  color: #212121;
  background: #F5F4FA;
  border-radius: 4px;
  border: none;
  padding: 6px 22px;
}
.port__nav-btn--active {
  color: #FFFFFF;
  background: #2196F3;
  box-shadow: 0px 3px 1px rgba(0, 0, 0, 0.1), 0px 1px 2px rgba(0, 0, 0, 0.08), 0px 2px 2px rgba(0, 0, 0, 0.12);
}
.port__nav-btn:hover {
  color: #FFFFFF;
  background: #2196F3;
